<template>
  <div id="pb-list2">
    <div class="pics">
      <div v-for="(item, index) in filteredItems" v-if="index<2">
        <img :src="item.img==null?'http://bpic.588ku.com/element_origin_min_pic/18/06/10/2a53a771f6f89141ac2a513ae34d9cac.jpg':item.img" alt="">
        <p :title="item.title" @click="routerLink(item.id,currentTab)">{{item.title}}</p>
      </div>
    </div>
    <ul>
      <li v-for="(item, index) in items" v-if="index<9 && index>1">
        <a class="list_title" :class="{ big: item.isBig }" @click="routerLink(item.id,currentTab)"> {{item.title}} </a>
        <b>{{item.postDate}}</b>
      </li>
    </ul>
  </div>
</template>
