              title: "",
              id: ""
            },
            {
              img:
                "http://122.13.4.239/resources/img/association_news/34d3fc47abdccad8.png",
              title: "2018",
              id: ""
            }
          ];
          break;//
        case "":
          data = [
            {
              img:
                "http://6979335.s21i-6.faiusr.com/2/ABUIABACGAAgoJzHxgUo9-XQvQQwwAc4gAo!600x600.jpg",
              title: "",
              id: ""
            },
            {
              img:
                "http://www.gdsjqr.com/attachment/cms/item/2018_05/16_17/250711c6397b4464.jpg",
              title: "",
              id: ""
            }
          ];
          break;//
        case "":
          data = [
            {
              img:
                "http://www.gdsjqr.com/attachment/cms/item/2018_09/10_09/9be272872aff037c.jpg",
              title: "AGV",
              id: ""
            },
