        }
      }
      return data;
    }
  },
  methods: {
    init() {
      if (this.myMessage) {  
        // console.log(this.myMessage);
        this.items = this.myMessage;
        this.items.forEach((item, index) => {
          this.items[index].postDate = item.postDate.slice(0, 10);
        });
        // console.log(this.items);
      }
    },
    routerLink(messageId, item) {  //
      let data = JSON.stringify(item);
      if (this.myMessage) {
        // console.log(this.currentTab.list);

        let url = "/news/industry_dynamic_text";
        if (this.currentTab.name == "") {
          url = "/news/industry_dynamic_text";
        }
        if (this.currentTab.name == "") {
          url = "/association/industry_dynamic_text";
        }
        if (this.currentTab.name == "") {
          url = "/product/industry_dynamic_text";
        }
        if (this.currentTab.name == "") {
          url = "/talent/talent_experience_text";
        }
        if (this.currentTab.list == "") {
          url = "/policy/policy_text";
        }
        if (this.currentTab.name == "") {
          url = "/enterprise/industry_dynamic_text";
        }
        this.$router.push({
          path: url,
          query: { messageId: messageId, item: data }
        });
      }
    }
  },
  watch: {
    isRequested() {
      this.init();
    }
  },
  activated() {
    this.init();
  },
  mounted() {
    this.init();
  }
};
</script>
<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin whole {
  width: 100%;
  height: 100%;
}
#pb-list2 {
  @include whole;
  border-bottom: #ccc thin solid;
}
.pics {
  float: left;
  margin: 5px 25px;
}
.pics img {
  padding-left: 15px;
  width: 160px;
  height: 100px;
}
.pics p {
  margin: 0;
  text-align: center;
  max-width: 160px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding: 1px 2px 3px 15px;
  font-size: 0.8em;
  cursor: pointer;
}
.pics p:hover {
  color: #127de7;
}
#pb-list2 ul {
  list-style-type: none;
  padding: 0 15px;
  text-align: left;
  float: left;
  width: 540px;
  box-sizing: border-box;
}
#pb-list2 ul li {
  background: none;
  padding: 0;
  margin: 0;
  border-top: none;
  line-height: 15px;
  padding: 8px 0 7px;
  font-size: 13px;
  clear: both;
}
.list_title {
  color: #3f3f3f;
  display: inline-block;
  max-width: 350px;
  overflow: hidden;
  padding: 0 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 15px;
  vertical-align: middle;
  cursor: pointer;
}
.list_title:hover {
  color: #127de7;
}
#pb-list2 ul li b {
  float: right;
  padding: 5px 15px;
  font-weight: 100;
  display: block;
  height: 15px;
  overflow: hidden;
  font-family: Verdana;
  text-overflow: ellipsis;
}
.big {
  color: #df171e;
  padding: 4px 0;
  font-size: 16px;
  padding: 5px 10px;
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



