<template>
  <div id="x_singlesidebar">
    <div class="x_singlesidebar">
      <div class="x_singlesiderbar_title">
        <a>{{tab_three1.name}}</a>
      </div>
      <span class="x_singlesiderbar_title_more" @click="routerLink(tab_three1.name,-1)" v-if="tab_three1.name!=''&&tab_three1.name!=''&&tab_three1.name!=''">
        <a></a>
      </span>
    </div>
    <div class="x_singlesiderbar_content">
      <h3
        class="x_singlesiderbar_title_list_first"
        @click="routerLink(tab_three1.name,list.message[0].id)"
        v-if="tab_three1.name!=''&&list.message[0].title != ''"
      >
        <a v-if="tab_three1.name !=''">{{this.list.message[0].title}}</a>
        <a v-if="tab_three1.name ==''">{{this.list.message[0].name}}</a>
      </h3> <!---->
      <div class="boxLoading" v-if="list.message[0].title == ''&&tab_three1.name!=''&&tab_three1.name!=''"></div>
      <li
        v-for="(li,index) of list.message"
        :key="index"
        class="x_singlesiderbar_li"
        v-if="tab_three1.name!=''&&index >= 1&& index <7&&list.message[0].title != ''&&tab_three1.name!=''" 
        @click="routerLink(tab_three1.name,list.message[index].id)"
      >
        <a v-if="tab_three1.name !=''">{{li.title}}</a>
        <a v-if="tab_three1.name ==''">{{li.name}}</a>
        <a></a>
      </li>
      <associationIntroduce v-if="tab_three1.name==''"></associationIntroduce>
      <el-carousel trigger="click" height="150px" v-if="tab_three1.name==''">
        <el-carousel-item v-for="(item,index) in imges" :key="index">
          <img :src="item.imgArray">
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>
<script>
import associationIntroduce from "@/components/common/association-introduce";
import PersonList from "@/components/common/person-list";

export default {
  components:{
    associationIntroduce,
    PersonList,
  },
  props: {
    tab_three1: {},
    isRequested: {
      type: Boolean,
      required: true
    }
  },
  watch: {
    //
    isRequested() {
      if (this.tab_three1.message) {
        this.list = this.tab_three1;
      }
    },
    tab_three1:{  //
      handler(){
      this.list = this.tab_three1;
    },
    deep:true,
    }
  },
  data() {
    return {
      title: "",
      list_first: "",
      list: {
        name: "",
        message: [
          { title: "" },
          { title: "" },
          { title: "" },
          { title: "" },
          { title: "" },
          { title: "" },
          { title: "" },
          { title: "" }
        ]
      },
      imges: [
        {
          imgArray: "./static/PICS/advertise1.jpg"
        },
        {
          imgArray: "./static/PICS/advertise2.jpg"
        },
        {
          imgArray: "./static/PICS/advertise3.jpg"
        },
        {
          imgArray: "./static/PICS/advertise4.jpg"
        }
      ]
    };
  },
  methods: {
    init() {
      if (this.tab_three1.message) {
        //
        this.list = this.tab_three1;
      }
    },
    routerLink(name, id) {
      let path;
      let url;  //
      let item = {
        newsUrl: "",
        list: "",
        name: "",
        requestPath: "",  //
        belong: ""
      };
      if (id != -1) {
        switch (name) {
          case "":
            url = "/information/findPolicyInfo";
            path = "/policy/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/information/findPolicyList";
            break; //
          case "":
            url = "/information/findInformationInfo";
            path = "/news/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/information/findInformationList";
            break; //
          case "":
            url = "/information/findReportInfo";
            path = "/news/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/information/findReportList";
            break; //
          case "":
            url = "/information/getHotspotInfo";
            path = "/news/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/information/findHotspotByPage";
            break; //
          case "":
            url = "/information/getAssociationNewsInfo";
            path = "/news/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/information/getAssociationNewsList";
            break; //
          case "":
            url = "/information/getNoticeInfo";
            path = "/news/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/information/getNoticeList";
            break; //
          case "":  //
            url = "/introduction/getMemberInfo";
            path = "/news/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/introduction/getMemberList";
            break; //
          case "":  //
            url = "/information/findReportInfo";
            path = "/news/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/information/findReportByPage";
            break; //
          case "":
            url = "/information/getRelatedHotInfo";
            path = "/news/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/information/getRelatedHot";
            break; //
          case "":
            url = "/information/getEducationTrainInfo";
            path = "/news/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/information/getEducationTrainList";
            break; //
        }
      }
      if (id == -1) { //
        switch (name) {
          case "":
            url = "/information/findPolicyInfo";
            path = "/policy";
            item.name = "";
            item.requestPath = "/information/findPolicyList";
            break; //
          case "":
            url = "/information/findInformationInfo";
            path = "/news/industry_dynamic";
            item.name = "";
            item.requestPath = "/information/findInformationList";
            break; //
          case "":
            url = "/information/findReportInfo";
            path = "/news/industry_dynamic";
            item.name = "";
            item.requestPath = "/information/findReportList";
            break; //
          case "":
            url = "/information/getHotspotInfo";
            path = "/news/industry_dynamic";
            item.name = "";
            item.requestPath = "/information/findHotspotByPage";
            break; //
          case "":
            url = "/information/getAssociationNewsInfo";
            path = "/association/industry_dynamic";
            item.name = "";
            item.requestPath = "/information/getAssociationNewsList";
            break; //
          case "":
            url = "/information/getNoticeInfo";
            path = "/association/industry_dynamic";
            item.name = "";
            item.requestPath = "/information/getNoticeList";
            break; //
          case "":
            url = "/introduction/getMemberInfo";  //
            path = "/association/industry_dynamic";
            item.name = "";
            item.requestPath = "/introduction/getMemberList";
            break; //
          case "":  //
            url = "/information/findReportInfo";
            path = "/association/industry_dynamic";
            item.name = "";
            item.requestPath = "/information/findReportByPage";
            break; //
          case "":
            url = "/information/getRelatedHotInfo";
            path = "/news/industry_dynamic_text";
            item.name = "";
            item.requestPath = "/information/getRelatedHot";
            break; //
          case "":
            url = "/information/getEducationTrainInfo";
            path = "/news/industry_dynamic";
            item.name = "";
            item.requestPath = "/information/getEducationTrainList";
            break; //
        }
      }
      item.newsUrl = url;
      item = JSON.stringify(item);
      if(name == ""&&id == -1){
        let association_part = {
          name: "",
          belong: "", //
          path: "/association/vip-list", //
          tocpn: "person-list", //
          message: null, //
          isActive: false,
          requestPath: "/introduction/getMemberList", //url
          newsUrl: "/introduction/getMemberInfo" //url
        };
        item = JSON.stringify(association_part);
        this.$router.push({ path: association_part.path, query: { item } }); //
      }else if(name == ""&&id != -1){
        let association_part = {
          name: "",
          belong: "", //
          path: "/association/vip-detail", //
          tocpn: "person-list", //
          message: null, //
          isActive: false,
          requestPath: "/introduction/getMemberList", //url
          newsUrl: "/introduction/getMemberInfo" //url
        };
        item = JSON.stringify(association_part);
        this.$router.push({ path: association_part.path, query: { item } }); //
      }else{
        this.$router.push({
          path: path,
          query: {
            item: item,
            messageId: id
          }
      });
      }
    }
  },
  activated() {
    this.init();
  },
  /*       mounted() {
    this.init();
    }    */
};
</script>

<style lang="scss" scoped>
#x_singlesidebar {
  height: inherit;
}
* {
  font: 13px/22px "";
}
.x_singlesiderbar_title_list_first a {
  font-size: 16px;
  width: 500px;
  height: 20px;
  padding-left: 15px;
  display: block;
  margin-top: 15px;
  font-weight: bold;
  color: #3f3f3f;
  cursor: pointer;
  text-overflow: ellipsis;
  margin-bottom: 15px;
  overflow: hidden;
  white-space: nowrap;
}
.x_singlesidebar {
  color: #3f3f3f;
  background: #fff;
  height: 31px;
  display: block;
  width: 100%;
  padding: 5px 0px;
}
.x_singlesiderbar_title {
  float: left;
  line-height: 31px;
}
.x_singlesiderbar_title_more {
  display: inline-block;
  float: right;
}
.x_singlesiderbar_title_more a {
  font-size: 14px;
  float: right;
  height: 31px;
  line-height: 31px;
  display: inline-block;
  margin-right: 15px;
  padding-left: 20px;
}
.x_singlesiderbar_title_more :before {
  content: "+";
  display: inline-block;
  color: #127de7;
  text-align: center;
  vertical-align: top;
  position: static;
  font-size: 20px;
  width: 20px;
  height: 29px;
  line-height: 27px;
  /* background: url(../images/picxi.png) no-repeat -3px -586px; */
  transition: all 0.2s;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -ms-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}
.x_singlesiderbar_title_more a {
  cursor: pointer;
}
.x_singlesiderbar_title a {
  color: #454545;
  text-align: center;
  height: 30px;
  line-height: 30px;
  display: block;
  padding: 0 15px;
  margin: 0 !important;
  background: #fff;
  color: #127de7 !important;
  font-weight: bold;
  font-size: 16px;
}
.x_singlesiderbar_content {
  min-height: 200px;
}
.x_singlesiderbar_li {
  font-weight: bold;
  width: auto;
  padding: 0 15px;
  height: 20px;
  font-size: 13px;
  padding: 15px;
  margin: 0 !important;
  border-top: 1px solid #dedede;
  padding: 3px 0;
  a {
    overflow: hidden;
    padding-left: 10px;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  a {
    padding-left: 15px;
  }
  &:hover {
    color: #e06a6a;
    cursor: pointer;
  }
}

/*loading css*/
.boxLoading {  
  width: 50px;
  height: 100px;
  margin: auto;
  position: relative;
  left: 0;
  right: 0;
  top: 80px;
  bottom: 0;
  &:before {
    content: '';
    width: 50px;
    height: 5px;
    background: #000;
    opacity: 0.1;
    position: absolute;
    top: 59px;
    left: 0;
    border-radius: 50%;
    animation: shadow .5s linear infinite;
  }
  &:after {
    content: '';
    width: 50px;
    height: 50px;
    background: #118dd7;
    animation: animate .5s linear infinite;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 3px;
  }
}

@keyframes animate {
  17% {
    border-bottom-right-radius: 3px;
  }
  25% {
    transform: translateY(9px) rotate(22.5deg);
  }
  50% {
    transform: translateY(18px) scale(1, .9) rotate(45deg);
    border-bottom-right-radius: 40px;
  }
  75% {
    transform: translateY(9px) rotate(67.5deg);
  }
  100% {
    transform: translateY(0) rotate(90deg);
  }
}
@keyframes shadow {
  0%, 100% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.2, 1);
  }
}
body, html {
  background: #ccc;
  overflow: hidden;
  height: 100%;
}
</style>




